<template>
  <section>
    <h1 class="data-title"><span>Data Edit</span></h1>
    <div class="data-url">
      <span>faker.kt.co.kr/{{ projectNamespace }}</span>
    </div>
    <article>
      <div class="data-input">
        <div class="data-name">
          <p>Data Name :</p>
          <input type="text" class="ds-input" v-model="dataName" :disabled="true" />
        </div>

        <div class="data-path">
          <p>Data Path :</p>
          <p class="data-path-host">
            <span>faker.kt.co.kr/{{ projectNamespace }}/</span>
          </p>
          <input type="text" class="ds-input" v-model="propsDataPath" :disabled="true" />
        </div>
      </div>
      <div class="data-input">
        <div class="data-rows">
          <p>Generate Rows :</p>
          <input type="number" class="ds-input" v-model="dataRows" @change="setDataRows" />
        </div>
        <div class="data-type">
          <p>Data Type :</p>
          <input type="text" class="ds-input" v-model="dataType" @change="setDataType" />
        </div>
      </div>
      <div class="data-array">
        <p>Array Check :</p>
        <span class="data-array-checkbox">
          <input type="checkbox" id="arrayCheck" value="true" />
          <label for="arrayCheck">True</label>
        </span>
      </div>
    </article>
  </section>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    projectNamespace: String,
    dataPath: String,
  },
  setup(props) {
    const store = useStore();
    const dataName = computed(() => store.getters.getDataName);

    const propsDataPath = ref(props.dataPath);
    store.commit('setDataPath', { dataPath: propsDataPath.value });

    let dataRows = computed(() => store.getters.getDataRows);
    let dataType = computed(() => store.getters.getDataType);

    // 선택한 data rows를 store에 저장
    const setDataRows = () => {
      store.commit('setDataRows', { dataRows: dataRows.value });
    };
    // 선택한 data type을 store에 저장
    const setDataType = () => {
      store.commit('setDataType', { dataType: dataType.value });
    };

    return {
      dataName,
      propsDataPath,
      dataRows,
      dataType,
      setDataRows,
      setDataType,
    };
  },
});
</script>
